import React from "react";
import { board, blackPiece, whitePiece } from './Graphics'

const PieceType = Object.freeze({
  EMPTY: 0,
  BLACK: 1,
  WHITE: 2
});

class Piece extends React.PureComponent {
  render() {
    const { type, x, y, ghost } = this.props;
    if (type === PieceType.EMPTY) return null;

    return (
      <svg width={30} height={30} x={7 + x * 36.25} y={7 + y * 36.25} opacity={ghost ? 0.67 : 1}>
        {type === PieceType.BLACK ? blackPiece : whitePiece}
      </svg>
    );
  }
}

export default class Board extends React.PureComponent {
  static styles = {
    container: {
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    }
  };

  constructor(props) {
    super(props);
    const pieces = new Array(15 * 15).fill(PieceType.EMPTY);
    this.state = {
      pieces,
      pieceToMove: PieceType.BLACK,
      hoverX: 0,
      hoverY: 0
    };
  }

  handleMouseDown = e => {
    const { x, y } = this.getBoardCoordinates(e);
    this.setState(({ pieces, pieceToMove }) => {
      if (pieces[x + y * 15] !== PieceType.EMPTY) return {};

      const newPieces = pieces.slice();
      newPieces[x + y * 15] = pieceToMove;
      return { pieces: newPieces, pieceToMove: PieceType.BLACK === pieceToMove ? PieceType.WHITE : PieceType.BLACK };
    });
  };

  handleMouseMove = e => {
    const { x, y } = this.getBoardCoordinates(e);
    this.setState({ hoverX: x, hoverY: y });
  };

  getBoardCoordinates = e => {
    const x = Math.round(Math.max(0, e.pageX - e.currentTarget.offsetLeft - 21) / 36.25);
    const y = Math.round(Math.max(0, e.pageY - e.currentTarget.offsetTop - 21) / 36.25);
    return { x, y };
  };

  render() {
    const { pieces, pieceToMove, hoverX, hoverY } = this.state;
    return (
      <div style={Board.styles.container}>
        <div
          style={{ position: "relative" }}
          onMouseDown={this.handleMouseDown}
          onMouseMove={this.handleMouseMove}
        >
          <svg width={550} height={550}>
            {board}
            {pieces.map((p, i) => <Piece key={i} type={p} x={i % 15} y={Math.floor(i/15)} />)}
            {pieces[hoverX + hoverY*15] === PieceType.EMPTY && <Piece ghost type={pieceToMove} x={hoverX} y={hoverY} />}
          </svg>
        </div>
      </div>
    );
  }
}
